@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600,700&display=swap);
body {
  margin: 0;
}

p, h2, span {
    color: #282828;
    font-family: 'Open Sans', sans-serif;
}

html {
    background: #E5E5E5;
}

.app-container {
    padding-bottom: 50px;
}

button {
    width: 140px;
    height: 40px;
    border-radius: 23px;
    background: #1e4091;
    color: #fff;
    box-shadow: none;
    border: none;
    font-size: 14px;
    font-weight: 600;
    outline: none;
    cursor: pointer;
}

.list-item {
    width: 122px;
    height: 122px;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 10px;
    margin-left: 10px;
    margin-bottom: 20px;
    position: relative;
}

.list-item-selected {
    box-shadow: 0px 0px 0px 4px rgba(0,0,0, 0.5) inset;
}

.list-item-selectable {
    cursor: pointer;
}

.list-item-name {
    color: #fff;
    font-weight: 600;
}

.list-item-trash-icon {
    display: none;
    position: absolute;
    right: 10px;
    top: 10px;
}

.list-item-trash-icon:hover {
    cursor: pointer;
}

.list-item-removable:hover .list-item-trash-icon {
    display: block;
}
.list {
    max-width: 90%;
    margin: 0 auto;
    margin-top: 20px;
    text-align: center;
}

.list-items {
    display: flex;
    flex-grow: 1;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: -10px;
    margin-right: -10px;
}

@media (min-width: 1100px) {
    .list {
        max-width: 1024px;
    }

    .list-items {
        justify-content: space-between;
    }
}
.palette-container {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
}

.palette-title {
    font-size: 16px;
    font-weight: 600;
}

.palette-remove {
    cursor: pointer;
    width: 122px;
    height: 122px;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 10px;
    margin-left: 10px;
    margin-bottom: 20px;
    position: relative;
    box-shadow: 0px 0px 15px -5px rgba(0,0,0,0.15);
    /* im not familiar with sketch and for some reason the grey trash can exported with a white bg, so i cant use the right bg color here */
    background-color: #FFF;
}

.palette-remove-trash-icon {
    fill: #D3D3D3;
}
.input-save-palette {
    width: 270px;
    height: 40px;
    background: #ffffff;
    box-shadow: 0px 0px 15px 5px rgba(0,0,0,0.15);
    border: none;
    border-radius: 4px;
    padding-left: 10px;
    padding-right: 10px;
    margin-right: 15px;
}

.text-save-palette {
    font-size: 16px;
    font-weight: 600;
}
.cart-heading {
    color: #282828;
    font-size: 24px;
    font-weight: 700;
}

.cart {
    max-width: 90%;
    margin: 0 auto;
    margin-top: 40px;
}

.cart-divider {
    height: 1px;
    max-width: 700px;
    width: 90%;
    margin-left: 0;
    margin-top: 50px;
    margin-bottom: 50px;
    background-color:#d5d5d5;
    border-color: #d5d5d5;
}

@media (min-width: 1100px) {
    .cart {
        max-width: 1024px;
    }
}
.header {
    background: #1e4091;
    box-shadow: 10px 10px 15px 0 rgba(0,0,0,0.15);
}

.header-icons {
    max-width: 90%;
    margin: 0 auto;
    display: flex;
    height: 60px;
}

.anchor {
    display: flex;
}

.anchor-cart-icon {
    margin-left: auto;
    position: relative;
}

.cart-count {
    position: absolute;
    right: -5px;
    top: 5px;
    height: 23px;
    width: 23px;
    background: #f13757;
    border-radius: 100%;
    color: #fff;
    border: 3px solid #1e4091;
    font-size: 12px;
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media (min-width: 1100px) {
    .header-icons {
        max-width: 1024px;
    }
}
